.wrapper{
    width: 96.5%;
    min-height: 100vh;
    background-color: #000000;
    padding-top: 130px;
    padding-bottom: 20px;
  }

  @media screen and (max-width: 768px) {
    .wrapper{
      padding-bottom: 0px;
      height: 90vh;
      width: 100%;
    }
  }

  @media screen and (min-width: 769px){
    .wrapper{
      padding: 30px;
      padding-top: 130px;
    }
  }


  .letterAndcontact{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
  }

  @media screen and (max-width: 768px){
    .letterAndcontact{
      flex-direction: column;
      align-items: flex-start;
      padding: 30px;
    }
  }

  .footerHeading{
    font-size: 18px;
    color: #ffffff;
  }

  .footersignInDescription{
    font-size: 13px;
    color: #ffffff;
  }

  .email{
    height: 25px;
    width: 300px;
    outline: none;
    border: none;
    padding: 10px;
    color: #ffffff;
  }

  @media screen and (max-width: 768px){
    .email{
      width: 200px;
      height: 20px;
      padding: 5px;
    }
  }

  .subscribeBtn{
    height: 45px;
    width: 140px;
    margin-left: 20px;
   text-transform: uppercase;
   background-color: transparent;
   outline: none;
   border: 1px solid;
   font-size: 14px;
   color: #ffffff;
   border-radius: 5px;
   cursor: pointer;
  }

  @media screen and (max-width: 768px){
    .subscribeBtn{
      width: 80px;
      height: 30px;
      font-size: 10px;
    }
  }

.contactDetails{
  padding-top: 30px;
}

.contactUsHeading{
  font-size: 20px;
  color: #ffffff;
  font-weight: 500;
  text-transform: uppercase;
}

.number{
  font-size: 15px;
  color: #ffffff;
}

.customerCare{
  font-size: 14px;
